import React from "react";

interface NecklaceSelectorProps {
  selected: string;
  onSelect: (url: string) => void;
}

const necklaceOptions = [
  {
    id: "necklace1",
    label: "Pearl Chain",
    imageUrl: "data:image/svg+xml;base64,"
  },
  {
    id: "necklace2",
    label: "Gold Pendant",
    imageUrl: "data:image/svg+xml;base64,"
  },
  {
    id: "necklace3",
    label: "Diamond Chain",
    imageUrl: "data:image/svg+xml;base64,"
  },
];

export default function NecklaceSelector({ selected, onSelect }: NecklaceSelectorProps) {
  return (
    <div className="flex space-x-4 overflow-auto">
      {necklaceOptions.map((option) => (
        <button
          key={option.id}
          onClick={() => onSelect(option.imageUrl)}
          className={`px-4 py-3 rounded-lg border transition-colors min-w-fit ${
            selected === option.imageUrl
              ? "border-primary bg-primary text-primary-foreground"
              : "border-muted hover:bg-muted/20"
          }`}
        >
          {option.label}
        </button>
      ))}
    </div>
  );
}
