"use client";

import React, { useEffect, useRef, useState } from "react";
import { useCamera } from "@/hooks/useCamera";
import BraceletSelector from "./BraceletSelector";
import NecklaceSelector from "./NecklaceSelector";

export default function VirtualTryOn() {
  const videoRef = useRef<HTMLVideoElement>(null);
  const { stream, error } = useCamera();
  
  // Default jewelry selections using SVG data URLs
  const [selectedBracelet, setSelectedBracelet] = useState<string>(
    "data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iODAiIGhlaWdodD0iODAiIHZpZXdCb3g9IjAgMCA4MCA4MCIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPGNpcmNsZSBjeD0iNDAiIGN5PSI0MCIgcj0iMzUiIHN0cm9rZT0iI0ZGRDcwMCIgc3Ryb2tlLXdpZHRoPSIxMCIgZmlsbD0ibm9uZSIvPgo8L3N2Zz4K"
  );
  
  const [selectedNecklace, setSelectedNecklace] = useState<string>(
    "data:image/svg+xml;base64,"
  );

  // Attach the camera stream to the video element
  useEffect(() => {
    if (videoRef.current && stream) {
      videoRef.current.srcObject = stream;
    }
  }, [stream]);

  if (error) {
    return (
      <div className="p-8 text-center">
        <div className="bg-destructive/10 border border-destructive/20 rounded-lg p-6 max-w-md mx-auto">
          <h3 className="text-lg font-semibold text-destructive mb-2">Camera Access Required</h3>
          <p className="text-sm text-muted-foreground mb-4">
            Unable to access camera. Please allow camera access and refresh the page to try on jewelry.
          </p>
          <p className="text-xs text-muted-foreground">
            Error: {error}
          </p>
        </div>
      </div>
    );
  }

  if (!stream) {
    return (
      <div className="p-8 text-center">
        <div className="bg-muted/50 rounded-lg p-6 max-w-md mx-auto">
          <h3 className="text-lg font-semibold mb-2">Loading Camera...</h3>
          <p className="text-sm text-muted-foreground">
            Please allow camera access to start trying on jewelry.
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="w-full max-w-2xl mx-auto">
      {/* Video Container with Overlays */}
      <div className="relative bg-black rounded-lg overflow-hidden mb-8">
        {/* Video feed */}
        <video
          ref={videoRef}
          className="w-full h-auto"
          autoPlay
          muted
          playsInline
        />

        {/* Jewelry Overlays */}
        {/* Bracelet overlay: positioned at the bottom right (simulate wrist area) */}
        <img
          src={selectedBracelet}
          alt="Bracelet overlay"
          className="absolute bottom-8 right-8 w-16 h-16 opacity-80"
          style={{ pointerEvents: "none" }}
        />
        
        {/* Necklace overlay: positioned near the top center (simulate neck area) */}
        <img
          src={selectedNecklace}
          alt="Necklace overlay"
          className="absolute top-12 left-1/2 transform -translate-x-1/2 w-24 h-16 opacity-80"
          style={{ pointerEvents: "none" }}
        />

        {/* Overlay Instructions */}
        <div className="absolute top-4 left-4 bg-black/50 text-white px-3 py-2 rounded-md text-sm">
          Position your hand and neck in view
        </div>
      </div>

      {/* Selection panels */}
      <div className="space-y-6">
        <div>
          <h2 className="text-lg font-semibold mb-3">Select Bracelet</h2>
          <BraceletSelector 
            selected={selectedBracelet} 
            onSelect={setSelectedBracelet} 
          />
        </div>
        
        <div>
          <h2 className="text-lg font-semibold mb-3">Select Necklace</h2>
          <NecklaceSelector 
            selected={selectedNecklace} 
            onSelect={setSelectedNecklace} 
          />
        </div>
      </div>

      {/* Instructions */}
      <div className="mt-8 p-4 bg-muted/30 rounded-lg">
        <h3 className="font-medium mb-2">How to use:</h3>
        <ul className="text-sm text-muted-foreground space-y-1">
          <li>• Position your hand in the bottom right area to see the bracelet</li>
          <li>• Keep your neck/chest area visible at the top center for the necklace</li>
          <li>• Select different jewelry styles using the buttons below</li>
          <li>• Move closer or further from the camera for the best fit</li>
        </ul>
      </div>
    </div>
  );
}
